(function () {
  'use strict';

  config.$inject = ["$stateProvider"];
  angular.module('login').config(config);

  function config($stateProvider) {
    $stateProvider.state('login', {
      url: '/login',
      templateUrl: 'login/login.tpl.html',
      controller: 'LoginCtrl',
      controllerAs: 'login',
      withoutAuthorization: true
    }).state('logout', {

      url: '/logout',
      template: 'Saliendo...',
      controller: ["AuthService", "$mdSidenav", function (AuthService, $mdSidenav) {

        $mdSidenav('left').close();

        AuthService.logout().then(function () {

          AuthService.clearSession();
        });
      }]

    });
  }
})();
(function () {
  'use strict';

  /**
   * @ngdoc object
   * @name login.controller:LoginCtrl
   *
   * @description
   *
   */

  LoginCtrl.$inject = ["$http", "$rootScope", "$state", "toastr", "AuthService"];
  angular.module('login').controller('LoginCtrl', LoginCtrl);

  function LoginCtrl($http, $rootScope, $state, toastr, AuthService) {

    var self = this;

    $rootScope.bglogin = true;

    this.init = function () {

      self.loginData = {

        email: 'admin@admin.com',

        password: 'admin'

      };
    };

    this.send = function () {

      AuthService.login(self.loginData).then(function (response) {

        if (response.data.err) {

          toastr.info('Email o Password incorrectos', 'Information');

          return;
        }

        sessionStorage['JWT'] = response.data.token;

        $rootScope.auth.logged = true;

        $rootScope.auth.user = response.data.user;

        $state.transitionTo('notifications');
      }, function (err) {

        $rootScope.$broadcast('AuthError');
      });
    };

    this.init();
  }
})();
(function () {
  'use strict';

  /**
   * @ngdoc service
   * @name login.factory:Auth
   *
   * @description
   *
   */

  AuthService.$inject = ["$rootScope", "$mdDialog", "$state", "AppService"];
  angular.module('login').factory('AuthService', AuthService);

  function AuthService($rootScope, $mdDialog, $state, AppService) {

    var AuthBase = {};

    AuthBase.login = function (data) {

      var config = {

        method: 'POST',

        skipAuthorization: true,

        data: data,

        url: '/api/v1/login'

      };

      return AppService.http(config);
    };

    AuthBase.verify = function () {

      var config = {

        method: 'POST',

        skipAuthorization: true,

        data: { token: sessionStorage['JWT'] },

        url: '/api/v1/verify'

      };

      return AppService.http(config);
    };

    AuthBase.clearSession = function () {

      $rootScope.auth = {};

      delete sessionStorage['JWT'];

      $state.transitionTo('login');
    };

    AuthBase.logout = function () {

      $rootScope.$broadcast('loading', false);

      return AppService.http({
        url: 'login',
        prefix: true,
        method: 'DELETE'
      });
    };

    return AuthBase;
  }
})();
